
@import "variables.less";
@import "listing_css.less";

#main-directory-details {
    .sidebar_second {
        padding-left: 15px;
        margin-top:10px;
    }
    .ds-1col {
        margin-top:10px;
    }
    .wrapper-details {
        margin-top:-8px;
    }
    .directory-wrapper {
        border-bottom:3px solid #1FAFB1;
    }
    .school-title-details {
        margin-bottom: 15px;
        margin-left: -15px;
        margin-right: 15px;
        margin-top: 15px;
        padding-top: 10px;
        font-size: 35px;
        a{
            color:@green_hard;
            text-decoration:none;
        }
    }
    .section_after_title{
      
        .image_section{
            img{
                width:120px;
                margin:auto;
            }
        }
        .right_side_section{           
            padding-right:0px;
            .txt_description {
                font-size: 19px;
                text-align: justify;
                font-weight: 500;
                color:@gray_hard;
            }
            .web_site_url{
                .modal{
                    #message_error_contact p{
                        
                        color:red;
                    }
                    .error_style{
                        border-color:red;
                        border:1px solid red;                       
                    }
                    .modal-header{
                                    background-color:@green_hard;
                                    color:white;
                                    border-top-left-radius: 5px;
                                    border-top-right-radius: 5px;
                        }
                    .btn-default{
                            color:white;
                            background-color:@green_hard;
                            border:1px solid @green_border;
                        }
                    .btn-default:focus{
                       outline:none;     
                    }
                }    
                .pull-left{
                    a{
                        font-size:20px;
                        color:@blue_soft;
                        text-decoration:none;
                    }
                }
                .pull-right{
                    .btn{
                        color:white;
                        background-color:@green_hard;
                        border:1px solid @green_border;
                        border-radius: 10px;
                        font-size: 16px;
                        font-weight: 300;
                    }
                    .btn:focus{
                        outline:none;
                    }
                
                }
            }

        }
    }
    .section_description{
        margin-top:20px;
        h2{
            font-size:22px;
            color: black;
        }
        p{
            font-size:16px;
            text-align:justify;
        }
    }
    .panel_details{
         
        .title_details h2{              
                
                font-size: 22px;
                color: black;
              
         }
        .wrapper-details-new {          
            margin-top: 20px;
        }
        .main_details {
            position: relative;
            padding-top:0px;
            margin-left: 15px;
        }
        .item_title {
            color:@green_hard;
            font-weight:bold;
           
        }
        .rating_section {
            position: absolute;
            right: -100%;
            margin-right: 170px;
        }
        .rating_section .item_title {
            width:60px;
        }
    }
    .adress_section{
          .sub_title {
            color:@gray_hard;
            font-weight:bold;            
          }
    }
    .download_section a{
            font-size: 15px;
            border: 1px solid @gray_soft;
            padding: 5px;
            text-decoration: none;
    }

}